import {useNavigation, useRoute} from '@react-navigation/native';
import {useAppSelector} from '../../store/hooks';
import {Animated, View} from 'react-native';
import {useMutation, useQueryClient} from 'react-query';
import {
  EditorComment,
  EditorProject,
  EncoderProject,
} from '../../types';
import {
  PanGestureHandler,
  TouchableOpacity,
  State,
} from 'react-native-gesture-handler';
import {Avatar, IconButton, List} from 'react-native-paper';
import React from 'react';
const minDuration = 40;

const EditorContentItem = ({
  item,
  onDragEnd,
  onDragStart,
  index,
  layerIndex,
  writeItem,
  parentDataHook,
}: {
  item: EncoderProject | EditorComment;
  folder: EditorProject;
}) => {
  const nav = useNavigation();
  const route = useRoute();
  const editor = useAppSelector(state => state.editor);
  const {pixelsPerSecond} = editor;
  const maxDuration = ((item?.duration || 0) - 1) * pixelsPerSecond;
  const start = item.encoding?.start || 0;
  const end = item.encoding?.end || item.duration;
  let currentDuration = end
    ? (end - start) * pixelsPerSecond
    : maxDuration;
  // if(currentDuration < minDuration)
  // 	currentDuration = minDuration / 2
  let width = new Animated.Value(currentDuration);
  const writeItemMutation = useMutation((item: EncoderProject) =>
    writeItem(item),
  );
  const onGestureEvent = Animated.event(
    [
      {
        nativeEvent: {
          translationX: width,
        },
      },
    ],
    {
      listener: event => {
        // console.info('ongesture', event)
      },
      useNativeDriver: true,
    },
  );
  const combIndex = index + layerIndex;
  let backgroundColor = combIndex % 2 ? 'darkcyan' : 'green';
  if (combIndex % 3 > 1) backgroundColor = 'darkslateblue';

  const deleteItem = async () => {
    parentDataHook.onRemoveFolder(item);
  };
  const onHandlerStateChange = async event => {
    if (event.nativeEvent.oldState === State.ACTIVE) {
      let newvalue = width._value;

      if (newvalue > maxDuration) newvalue = maxDuration;
      if (newvalue < minDuration) newvalue = minDuration;

      if (width._value !== newvalue) width.setValue(newvalue);

      if (!item.encoding) item.encoding = {};
      newvalue = newvalue / pixelsPerSecond;
      if (newvalue < item.encoding?.start || 0)
        newvalue += item.encoding?.start;
      item.encoding.end = newvalue;
      // item.encoding.lastUpdated = new Date().valueOf()
      await writeItemMutation.mutateAsync(item);
    }
  };
  return (
    <View key={item.id} style={{height: 68, position: 'relative'}}>
      <Animated.View
        style={{
          width: !item.isContact
            ? width.interpolate({
                inputRange: [start, maxDuration],
                outputRange: [start, maxDuration],
                extrapolate: 'clamp',
              })
            : 75,
          height: '100%',
          top: 0,
          padding: 0,
          margin: 0,
          left: item.currentPosition
            ? pixelsPerSecond * item.currentPosition
            : 0,
          position: item.currentPosition ? 'absolute' : 'relative',
          backgroundColor,
          zIndex: 2,
          borderWidth:
            item.id === route.params.encoder ||
            item.id === route.params.chatId
              ? 1
              : 0,
          borderRadius: item.isContact ? 100 : 0,
          borderColor: 'white',
        }}>
        {!item.isContact && (
          <>
            {/* <PanGestureHandler
              onGestureEvent={onGestureEvent}
              onHandlerStateChange={onHandlerStateChange}>
              <View
                style={{
                  position: 'absolute',
                  left: 0,
                  bottom: 0,
                  transform: [{rotate: '90deg'}],
                  cursor: 'pointer',
                  zIndex: 100,
                }}>
                <List.Icon
                  icon={'resize-bottom-right'}
                  style={{padding: 0, margin: 0}}
                />
              </View>
            </PanGestureHandler> */}
            <PanGestureHandler
              onGestureEvent={onGestureEvent}
              onHandlerStateChange={onHandlerStateChange}>
              <View
                style={{
                  position: 'absolute',
                  right: 0,
                  bottom: 0,
                  cursor: 'pointer',
                  zIndex: 100,
                }}>
                <List.Icon
                  icon={'resize-bottom-right'}
                  style={{padding: 0, margin: 0}}
                />
              </View>
            </PanGestureHandler>
          </>
        )}
        <List.Item
          onPress={() =>
            nav.setParams({
              ...route.params,
              encoder: !item.isContact && item.id,
              chatId: item.isContact && item.id,
            })
          }
          style={{height: '100%'}}
          title={item.isContact ? '' : item.title}
          right={() => (
            <>
              {!item.isContact && (
                <TouchableOpacity
                  onPressIn={onDragStart}
                  onPressOut={onDragEnd}
                  style={{pointerEvents: 'auto'}}>
                  <IconButton size={18} icon={'drag'} />
                </TouchableOpacity>
              )}
              {item.isContact && (
                <Avatar.Text
                  size={30}
                  label={[
                    item.title?.split(' ')[0][0],
                    item.title?.split(' ')[1][0],
                  ].join('')}
                  style={{
                    marginLeft: -10,
                    marginTop: 8,
                    backgroundColor: 'transparent',
                  }}
                />
              )}
              <IconButton
                size={18}
                icon={'delete'}
                onPress={deleteItem}
              />
            </>
          )}
        />
      </Animated.View>
    </View>
  );
};

export default EditorContentItem;
