import React, {useRef} from 'react';
import {
  Image,
  ImageBackground,
  Platform,
  View,
  useWindowDimensions,
} from 'react-native';
import {
  ActivityIndicator,
  Banner,
  Divider,
  IconButton,
  useTheme,
} from 'react-native-paper';
import {useMutation, useQuery} from 'react-query';
import {Media} from '../../../types';
import {getFileOpfs, getVideoWT} from '../../../api/media';
import {useIsFocused} from '@react-navigation/native';
import {
  MediaActionType,
  loaded,
  togglePause,
} from '../../../store/slices/media';
import {useAppDispatch} from '../../../store/hooks';
import {ResizeMode, Video} from 'expo-av';
import {getById} from '../../../api';
import {useMediaPlayer} from '../../../hooks/useMediaPlayer';
import {UnifiedStackParam} from '../../../types/nav';

export default function MediaBanner({
  height,
  mediaPlayer,
}: {
  mediaPlayer: ReturnType<typeof useMediaPlayer>;
  height: number;
}) {
  const theme = useTheme();
  const dispatch = useAppDispatch();
  const {currentMediaId, currentMedia} = mediaPlayer;
  const fileType = currentMedia?.fileType;
  const isImage = fileType?.search('image') > -1;
  const {width} = useWindowDimensions();
  const focused = useIsFocused();
  const mediaRes = currentMedia as Media;
  const updateMediaTime = useMutation(
    async (currentTime: number) => {
      if (mediaRes) {
        // mediaRes.currentPosition = currentTime;
        // await writeItem(mediaRes)
        // await mediaData.refetch()
      }
    },
    {retry: false},
  );

  const mediaQuery = useQuery(
    'wtMedia_' + currentMediaId,
    async () => {
      let file: string | undefined;

      try {
        file = await getFileOpfs(mediaRes);
      } catch (err) {}
      if (!file && mediaRes.url?.search('https://') > -1)
        file = mediaRes.url;
      if (!file) file = (await getVideoWT(mediaRes)).stream;

      dispatch(
        loaded({
          actionType: MediaActionType.loaded,
          mediaId: currentMediaId!,
        }),
      );
      return file;
    },
    {
      enabled: focused ? true : false,
      refetchOnMount: true,
    },
  );
  const mediaLoading = mediaQuery.isLoading;
  const mediaSrc = mediaQuery?.data;
  const currentPosition = mediaRes?.currentPosition || 0;
  const isMobile = width < 700;
  const bannerHeight = isMobile ? height / 4 : height / 2;
  const videoRef = useRef();
  return (
    <>
      <Banner
        visible={currentMediaId ? true : false}
        theme={theme}
        style={{
          backgroundColor: 'black',
          padding: 0,
          margin: 0,
          width,
          height: bannerHeight,
          position: 'static',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
        }}
        contentStyle={{
          width: isMobile ? width : '100%',
          height: bannerHeight,
          padding: 0,
          margin: 0,
        }}>
        <View
          style={{
            width: isMobile ? width : '100%',
            height: bannerHeight - 40,
            position: 'relative',
          }}>
          {isImage && currentMediaId && mediaSrc && (
            <ImageBackground
              style={{height: bannerHeight}}
              resizeMethod="scale"
              imageStyle={{resizeMode: 'contain'}}
              // imageStyle={{width: '100%', height: '100%'}}
              source={{uri: mediaSrc}}
            />
          )}
          {!isImage && currentMediaId && mediaSrc ? (
            Platform.OS === 'web' ? (
              <video
                // autoPlay={!media.isMediaPaused ? true : false}
                controls={true}
                style={{
                  width: '100%',
                  height: '100%',
                  objectFit: 'contain',
                }}
                onPlaying={e => {
                  const {currentTime} = e.nativeEvent
                    .target as any as {currentTime: number};
                  updateMediaTime.mutateAsync(currentTime);
                  dispatch(
                    togglePause({
                      pause: false,
                      actionType: MediaActionType.pause,
                    }),
                  );
                }}
                onPause={e => {
                  dispatch(
                    togglePause({
                      pause: true,
                      actionType: MediaActionType.pause,
                    }),
                  );
                }}
                onEnded={p => {
                  console.info('ended', p);
                }}
                onTimeUpdate={e => {
                  const {currentTime} = e.nativeEvent
                    .target as any as {currentTime: number};
                  // console.info('time', currentTime)
                  const minimumTimeDiff = 5;
                  if (
                    currentTime > minimumTimeDiff &&
                    currentTime - currentPosition > minimumTimeDiff &&
                    !updateMediaTime.isLoading
                  ) {
                    updateMediaTime.mutateAsync(currentTime);
                  }
                }}
                onResize={() => {
                  console.info('resize');
                }}
                onLoadedData={e => {
                  if (!mediaPlayer.isMediaPaused)
                    e.nativeEvent.target.play();
                }}
                ref={el => {
                  // console.info("onmount", el)
                  if (
                    el &&
                    el.paused &&
                    el.currentTime !== currentPosition
                  ) {
                    // el.currentTime = currentPosition
                  }
                }}
                src={mediaSrc}>
                <track
                  label="language one"
                  kind="subtitles"
                  srcLang="en"
                  default
                />
              </video>
            ) : (
              <Video
                source={{uri: mediaSrc}}
                ref={videoRef}
                shouldPlay={true}
                style={{
                  width: '100%',
                  height: '100%',
                  position: 'absolute',
                  left: -10,
                  top: 0,
                  padding: 0,
                  margin: 0,
                }}
                volume={1}
                resizeMode={ResizeMode.CONTAIN}
                useNativeControls={true}
                onPlaybackStatusUpdate={async status => {
                  if (status.isLoaded) {
                    // console.info('is loaded');
                    if (status.isBuffering && !status.isPlaying) {
                      // console.info('is buffering and paused');
                    }
                  }
                }}
              />
            )
          ) : null}
          {mediaLoading ? (
            <ActivityIndicator
              style={{position: 'relative', flexBasis: 'auto'}}
              size={72}
            />
          ) : null}
          <IconButton
            style={{
              position: 'absolute',
              cursor: 'pointer',
              right: Platform.select({native: 10, web: 0}),
              top: Platform.select({native: -20, web: 0}),
              pointerEvents: 'auto',
              zIndex: 100000,
            }}
            icon={'close'}
            mode="outlined"
            size={25}
            onPress={() => mediaPlayer.onMediaPress('')}
          />
        </View>
      </Banner>
      <Divider />
    </>
  );
}
