import React from 'react';
import {View} from 'react-native';
import {TextInput} from 'react-native-paper';
import {useAppDispatch, useAppSelector} from '../../store/hooks';
import {
  setAuctioName,
  setAuctionPrice,
  setAuctionWallet,
} from '../../store/slices/auction';
import {MetamaskAccountsButton} from './metamaskaccountsbutton';

export function SellDialogBody() {
  const dispatch = useAppDispatch();
  const name = useAppSelector(state => state.auction.newAuctionName);
  const price = useAppSelector(
    state => state.auction.newAuctionPrice,
  );
  const wallet = useAppSelector(
    state => state.auction.newAuctionWallet,
  );
  const namePlaceholder = 'Auction name';
  const walletPlaceholder = 'Auction ERC20 Wallet';
  const pricePlaceholder = 'Minimum Price in Matic';
  return (
    <View style={{width: '100%', height: 'auto'}}>
      <TextInput
        placeholder={namePlaceholder}
        label={namePlaceholder}
        style={{width: '100%'}}
        value={name}
        onChangeText={text => dispatch(setAuctioName(text))}
        mode={'outlined'}
      />

      <TextInput
        label={pricePlaceholder}
        placeholder={pricePlaceholder}
        style={{width: '100%'}}
        keyboardType="number-pad"
        value={price.toString()}
        onChangeText={value =>
          dispatch(setAuctionPrice(parseFloat(value)))
        }
        mode={'outlined'}
      />
      <TextInput
        label={walletPlaceholder}
        placeholder={walletPlaceholder}
        style={{width: '100%'}}
        value={wallet}
        onChangeText={text => dispatch(setAuctionWallet(text))}
        mode={'outlined'}
      />
      <MetamaskAccountsButton type="sell" />
    </View>
  );
}
