export const genRenderCmd = (
  projectData: EditorProject,
  layers: EditorLayer[],
  isPrerender?: boolean,
) => {
  if (projectData?.id === undefined || layers?.length < 1)
    return {execCmd: [], medias: [], files: []};
  const fileId = `${projectData.id}_${projectData.title}`;
  const {editor: encoding} = projectData;
  const outPostfix = `.output.${encoding?.lastUpdated || 0}.${
    encoding?.output || 'mp4'
  }`;
  const outFileId = fileId + outPostfix;
  const threads =
    Platform.OS === 'web' &&
    window.navigator.hardwareConcurrency.toString();
  const extraCode: string[] =
    encoding?.code && encoding.code[0] != ''
      ? encoding.code
          .filter(i => i !== '')
          .map(i =>
            i
              .trim()
              .replace(/[\r\n]+/gm, '')
              .trim(),
          )
      : [];
  const medias = layers
    ?.map(layer => layer?.media?.map(m => ({...m, layer})))
    .flat()
    .filter(i => i)
    .filter(i => !i.isContact);
  const files =
    medias
      ?.map(e => [
        '-i',
        `${documentDir}${e?.id}_${e?.filename}.output.${
          e?.encoding?.lastUpdated || 0
        }.${e?.encoding?.output || 'mp4'}`,
      ])
      .flat() || [];
  const hasAudio = layers?.reduce((a, layer) => {
    if (a === true) return a;

    return !layer.editor?.muted;
  }, false);
  const hasVideo = layers?.reduce((a, layer) => {
    if (a === true) return a;

    return !layer.editor?.videoDisabled;
  }, false);
  const audioOutput = hasAudio ? '[a_concat]' : '';
  const audioMap = hasAudio ? ['-map', audioOutput] : [];
  const videoOutput = hasVideo ? '[v_concat]' : '';
  const videoMap = hasVideo ? ['-map', videoOutput] : [];
  const aspectRatio = projectData?.editor?.aspectRatio || '16/9';
  const scale = `scale=${encoding?.width || 1280}:${
    encoding?.height || 720
  },setdar=${aspectRatio}`;
  const filterTrim =
    medias?.map((e, index) => {
      const trimV =
        `[${index}:v]trim=start=${
          e?.encoding.start || 0
        }:end=${parseInt(
          e?.encoding?.end || e?.duration,
        )},setpts=PTS-STARTPTS` +
        ',' +
        scale +
        `[v${index}]`;

      const trimA = `[${index}:a]atrim=start=${
        e?.encoding.start || 0
      }:end=${parseInt(
        e?.encoding?.end || e?.duration,
      )},asetpts=PTS-STARTPTS[a${index}]`;
      //`,volume=${e?.encoding?.soundVolume/100 || 1},amix[a${index}]`
      let trim = trimV + ', \n' + trimA;
      if (e?.layer?.editor?.muted) trim = trimV;
      if (e?.layer?.editor?.videoDisabled) trim = trimA;
      return trim;
    }) || [];
  // console.info('medias', medias)
  const videosLength =
    medias?.filter(i => i?.layer?.editor?.videoDisabled !== true)
      .length || 1;
  const audiosLength =
    medias?.filter(i => i?.layer?.editor?.muted !== true).length || 1;
  const videoConcat =
    (medias
      ?.map((a, i) => {
        if (a.layer?.editor?.videoDisabled !== true) return `[v${i}]`;
        return undefined;
      })
      ?.filter(i => i)
      .join('') || '') +
    `concat=n=${videosLength}:v=1:a=0${videoOutput}`;
  const audioConcat =
    (medias
      ?.map((a, i) => {
        if (a.layer?.editor?.muted !== true) return `[a${i}]`;
        return undefined;
      })
      ?.filter(i => i)
      ?.join('') || '') +
    `concat=n=${audiosLength}:v=0:a=1${audioOutput}`;
  // [a3]volume=enable='between(t,0,49)'${audioOutput}`
  const filterConcat = videoConcat + ';\n' + audioConcat;
  filterTrim.push(filterConcat);
  const execCmd: string[] = [
    ...files,
    '-crf',
    encoding?.compressionLevel?.toString() || '20',
    '-threads',
    threads,
    '-filter_complex',
    `${filterTrim.join(';\n')}`,
    '-preset',
    encoding?.preset || 'veryfast',
    '-movflags',
    '+faststart',
    ...extraCode,
    ...videoMap,
    ...audioMap,
    '-r',
    encoding?.framerate?.toString() || '30',
    `${documentDir}${outFileId}`,
  ];
  return {execCmd, medias, files, outFileId};
};

export const genExecCmd = (
  projectData: EncoderProject,
  isPrerender?: boolean,
) => {
  if (projectData?.id === undefined) return [];
  const fileId = `${projectData.id}_${projectData.filename}`;
  const {encoding} = projectData;
  const outPostfix = `.output.${encoding?.lastUpdated || 0}.${
    encoding?.output || 'mp4'
  }`;
  const outFileId = fileId + outPostfix;
  const threads = window.navigator.hardwareConcurrency.toString();
  const extraCode: string[] =
    encoding?.code && encoding.code[0] != ''
      ? encoding.code
          .filter(i => i !== '')
          .map(i =>
            i
              .trim()
              .replace(/[\r\n]+/gm, '')
              .trim(),
          )
      : [];
  const prerenderOptionalCode = !isPrerender
    ? [
        '-ss',
        genExecTime(encoding?.start || 0),
        '-to',
        genExecTime(encoding?.end || projectData?.duration),
      ]
    : [];
  const execCmd = [
    '-i',
    `${documentDir}${fileId}`,
    '-crf',
    encoding?.compressionLevel?.toString() || '20',
    '-threads',
    threads,
    '-filter_complex',
    `scale=${encoding?.width || projectData.width}x${
      encoding?.height || projectData.height
    }`,
    '-preset',
    encoding?.preset || 'veryfast',
    '-movflags',
    '+faststart',
    ...prerenderOptionalCode,
    ...extraCode,
    `${documentDir}${outFileId}`,
  ];
  return execCmd;
};

export const genExecTime = (seconds: number, comma?: boolean) => {
  const end = parseInt(seconds, 10);
  let endMins = parseInt(end / 60, 10);
  let endSeconds = parseInt(end - endMins * 60, 10);
  if (endMins.toString().length === 1) endMins = '0' + endMins;
  if (endSeconds.toString().length === 1)
    endSeconds = '0' + endSeconds;
  const separator = comma ? ',' : ':';
  const extraSymbol = comma ? '' : '00:';
  return `${extraSymbol}${endMins}${separator}${endSeconds}`;
};

export const getActiveProjectLayer = (
  layers: EditorLayer[],
  currentPosition: number,
  type: 'audio' | 'video' = 'video',
) => {
  let activeMedia: EncoderProject | undefined;
  let layerCount = 0;
  let offset = 0;
  let layer: EditorLayer | undefined;

  while (activeMedia?.id === undefined) {
    layer = layers[layerCount];

    if (layerCount > layers.length + 1) {
      console.error('layers', layers, layerCount);
      throw 'no_layer_found';
    }

    if (layer?.id === undefined) {
      layerCount++;
      continue;
    }

    if (type === 'video' && layer?.editor?.videoDisabled === true) {
      layerCount++;
      continue;
    }

    if (type === 'audio' && layer?.editor?.muted === true) {
      layerCount++;
      continue;
    }

    const layerMedia = layer.media;
    activeMedia = layerMedia!.reduce((a, media: EncoderProject) => {
      if (a.id) return a;

      a +=
        (media?.encoding?.end || media?.duration) -
        (media?.encoding?.start || 0);
      if (a >= currentPosition) return media;

      offset = a;
      return a;
    }, 0);
    layerCount++;
  }
  return {offset, layer, activeLayer: layerCount, activeMedia};
};

export const getNextActiveProjectMedia = (
  layers: EditorLayer[],
  startPosition: number,
  media: EncoderProject,
) => {
  const currentDuration =
    (media?.encoding?.end || media?.duration) -
    (media?.encoding?.start || 0);
  const nextPosition = startPosition + currentDuration + 1;
  const next = getActiveProjectLayer(layers, nextPosition);
  console.info('nextPosition', nextPosition, next);
  return next;
};

export const getTotalTime = (layers: EditorLayer[]): number => {
  const time = layers.reduce((a, layer) => {
    const t = layer.media?.reduce((agv, media) => {
      agv +=
        (media?.encoding?.end || media?.duration) -
        (media?.encoding?.start || 0);
      return agv;
    }, 0);
    if (!t) return a;
    if (a > t) return a;
    if (t > a) return t;
  }, 0);
  return time;
};

const genRegenCmd = ({
  input,
  width,
  height,
  output,
}: {
  input: string;
  width: number;
  height: number;
  output: string;
}) =>
  `ffmpeg -loop 1 -i ${input}  -c:v libx264 -t 15 -pix_fmt yuv420p -vf scale=${width}:${height} ${output}`;

