import {useMutation, useQueryClient} from 'react-query';
import {Auction, Contact, UserInfo} from '../types';
import {useMemo} from 'react';
import {
  NavigationProp,
  useNavigation,
  useRoute,
} from '@react-navigation/native';
import {
  AuctionParams,
  ChatParams,
  ChatStackParams,
} from '../types/nav';
import {sendChatMessage, writeItem} from '../api';

export const useRouteAuction = (
  contact: Auction | undefined,
  isLoading: boolean,
) => {
  const queryClient = useQueryClient();
  const route = useRoute();
  const {auctionId, title, mnemonic} = route.params as AuctionParams;
  const createContactMutation = useMutation(
    async () => {
      const nauction = {
        id: auctionId,
        isContact: true,
        folderId: auctionId,
        name: title,
        mnemonic,
      };
      console.info('nauction', nauction);
      // await writeItem(nauction);
    },
    {
      retry: true,
      onSuccess: async () => {
        try {
          await queryClient.refetchQueries('auction_' + auctionId);
          await queryClient.refetchQueries(
            'current_bid_' + auctionId,
          );
          // await queryClient.refetchQueries('current_bid_' + auctionId);
          // await sendChatMessage(
          //   auctionId,
          //   user.name + ' has joined',
          //   mnemonic,
          //   {
          //     system: true,
          //   },
          // );
        } catch (err) {
          console.error('onadd contact error', err);
        }
      },
    },
  );

  useMemo(() => {
    if (!isLoading && !contact && !createContactMutation.isLoading) {
      createContactMutation.mutateAsync().then(async () => {
        console.info('after create auction');
      });
    }
  }, [isLoading, contact, createContactMutation]);
};
