import React from 'react';
import {
  View,
  GestureResponderEvent,
  useWindowDimensions,
} from 'react-native';
import {useFolder} from '../../hooks/useFolder';
import {
  EditorComment,
  EditorLayer,
  EncoderProject,
  Folder,
  FolderType,
} from '../../types';
import {Divider, IconButton, List, Text} from 'react-native-paper';

import DragList from 'react-native-draglist';
import {TouchableOpacity} from 'react-native-gesture-handler';

import {useAppDispatch, useAppSelector} from '../../store/hooks';
import uuid from 'react-native-uuid';
import {
  createNewParty,
  DocumentPicker,
  getById,
  getFolderContent,
  getUserData,
  writeItem,
} from '../../api';
import {startOnboarding} from '../../store/slices/upload';
import EditorContentItem from './editorcontentitem';

export function EditorLayerHeaderComponent(props) {
  const dataHook = useFolder(
    props.folderId,
    getFolderContent,
    itemId => getById(itemId, props.parentFolderId),
    FolderType.editor,
    writeItem,
  );
  const {width} = useWindowDimensions();
  const isMobile = width < 700;
  const folder = dataHook.folder as EditorLayer;
  const layer = folder?.editor;
  const showComments = useAppSelector(
    state => state.editor.showComments,
  );
  const caretPosition = useAppSelector(
    state => state.editor.caretPosition,
  );
  const scrollPosition = useAppSelector(
    state => state.editor.scrollPosition,
  );
  const pixelsPerSecond = useAppSelector(
    state => state.editor.pixelsPerSecond,
  );
  const currentPosition =
    (caretPosition + scrollPosition) / pixelsPerSecond;
  const toggleLayerProp = async (propName: keyof typeof layer) => {
    await writeItem({
      ...dataHook.folder,
      editor: {
        ...(layer || {}),
        [propName]: layer && layer[propName] === true ? false : true,
      },
    } as EditorLayer);
    dataHook.folderData.refetch();
  };

  const muteLayer = (_e: GestureResponderEvent) =>
    toggleLayerProp('muted');

  const hideLayer = (_e: GestureResponderEvent) =>
    toggleLayerProp('videoDisabled');

  const isCommentLayer =
    folder?.id !== undefined && layer?.isCommentLayer;

  if (isCommentLayer && !showComments) return <></>;
  const Controls = (p = {}) => (
    <View style={{flexDirection: 'row', padding: 0}}>
      <IconButton
        {...p}
        size={iconSize}
        icon={layer?.muted ? 'volume-off' : 'volume-high'}
        mode={'outlined'}
        onPress={muteLayer}
      />
      <IconButton
        size={iconSize}
        {...p}
        mode={'outlined'}
        icon={layer?.videoDisabled ? 'video-off' : 'video'}
        onPress={hideLayer}
      />
      <IconButton
        size={iconSize}
        {...p}
        icon={'delete'}
        onPress={async () => {
          await dataHook.onRemoveFolder({
            id: props.folderId,
          } as Folder);
          props.parentHook.folderContentQuery.refetch();
        }}
        mode={'outlined'}
      />
    </View>
  );
  const iconSize = isMobile ? 10 : 14;

  const onClickAddComment = async () => {
    const user = await getUserData();
    const id = uuid.v4().toString();
    const mnemonic = props.parentHook.mnemonic;
    const comment: EditorComment = {
      id,
      partyId: id,
      userId: user.id,
      title: user.name,
      currentPosition,
      isContact: true,
      isParty: true,
      authorName: user.name,
      name: user.name,
      mnemonic,
      folderId: props.projectId + '_comments',
      isFolder: true,
      duration: 10,
      encoding: {
        start: 0,
        end: 10,
      },
    };
    await createNewParty(comment);
    dataHook.folderContentQuery.refetch();
  };
  return (
    <View style={{display: 'flex', flexDirection: 'row', height: 68}}>
      <View style={{width: '100%'}}>
        <Divider />
        <List.Item
          title={() =>
            isMobile ? (
              <Controls />
            ) : (
              <TouchableOpacity
                onPressIn={props.onDragStart}
                onPressOut={props.onDragEnd}
                style={{pointerEvents: 'auto'}}>
                <Text style={{fontSize: 14}}>
                  {dataHook.folder?.title}
                </Text>
              </TouchableOpacity>
            )
          }
          right={p => {
            if (isMobile) return null;
            if (!isCommentLayer) return <Controls />;

            return (
              <IconButton
                size={iconSize}
                {...p}
                mode={'outlined'}
                icon={'comment-plus'}
                onPress={onClickAddComment}
              />
            );
          }}
        />
      </View>
    </View>
  );
}

export function EditorLayerBodyComponent(props) {
  const dispatch = useAppDispatch();
  const dataHook = useFolder(
    props.folderId,
    getFolderContent,
    itemId => getById(itemId, props.parentFolderId),
    FolderType.editor,
    writeItem,
  );
  const folder = dataHook.folder as EditorLayer;
  const layer = folder?.editor;
  const showComments = useAppSelector(
    state => state.editor.showComments,
  );
  const isCommentLayer =
    folder?.id !== undefined && layer?.isCommentLayer;
  if (isCommentLayer && !showComments) return <></>;

  const renderContent = ({
    item,
    onDragEnd,
    onDragStart,
    index,
  }: {
    item: EncoderProject;
  }) => (
    <EditorContentItem
      onDragEnd={onDragEnd}
      onDragStart={onDragStart}
      item={item}
      index={index}
      layerIndex={props.layerIndex}
      writeItem={writeItem}
      parentDataHook={dataHook}
    />
  );
  const onClickAddContent = async () => {
    const pickerResult = await DocumentPicker.pickSingle({
      presentationStyle: 'fullScreen',
    });
    console.info('pickerResult', pickerResult, dataHook.folder);
    // setMediaModalVisible(true)
    if (!pickerResult) return;
    const url = pickerResult.uri;
    let fId = props.folderId;
    const nd: Media = {
      id: uuid.v4().toString(),
      url,
      title: pickerResult.name,
      originalId: pickerResult.name,
      filename: pickerResult.name,
      fileType: pickerResult.type,
      folderId: fId,
      userId: props.userId,
      date: new Date().valueOf(),
      isMediaNew: false,
      isEncoderProject: true,
      type: pickerResult.file.type,
      size: pickerResult.file.size,
      extractor: dataHook.folder?.mnemonic ? 'wt' : 'local',
    };
    dispatch(startOnboarding({file: pickerResult.file, media: nd}));
  };
  const data = dataHook.folderContent;
  return (
    <View style={{display: 'flex', flexDirection: 'row'}}>
      <View
        style={{width: '80%', display: 'flex', flexDirection: 'row'}}>
        {!isCommentLayer && data !== undefined && (
          <>
            <DragList
              horizontal={true}
              renderItem={renderContent}
              keyExtractor={item => item.id || item.date.toString()}
              data={data}
              onReordered={dataHook.onReordered}
              style={{width: '100%'}}
              showsHorizontalScrollIndicator={false}
              showsVerticalScrollIndicator={false}
            />
            <List.Item
              title={'Add Content'}
              titleStyle={{fontSize: 14}}
              style={{pointerEvents: 'auto'}}
              onPress={() => onClickAddContent()}
              right={props => (
                <>
                  <IconButton
                    {...props}
                    icon={'plus'}
                    onPress={() => onClickAddContent()}
                    mode={'outlined'}
                  />
                </>
              )}
            />
          </>
        )}
        {isCommentLayer && data !== undefined && (
          <>
            {data?.map((item, index) => (
              <EditorContentItem
                item={item}
                index={index}
                layerIndex={props.layerIndex}
                writeItem={writeItem}
                parentDataHook={dataHook}
              />
            ))}
            <List.Item
              title={''}
              titleStyle={{fontSize: 14}}
              style={{pointerEvents: 'auto', height: 68}}
              // right={(p)=><IconButton size={14} {...p} mode={"outlined"} icon={"forum"} /> }
            />
          </>
        )}
      </View>
    </View>
  );
}
