import React, {memo} from 'react';
import {createStackNavigator} from '@react-navigation/stack';

import FolderPage from '../components/folder/folderpage';
import {FolderType} from '../types';
import FolderLibrary from '../components/folder/folderlibrary';
import {useFolders} from '../hooks/useFolders';
import EncoderProject from '../components/encoder/encoderproject';
import {useRoute} from '@react-navigation/native';
import {getFolderContent, getFoldersByType, writeItem} from '../api';
import {EncoderStackParams} from '../types/nav';
const folderType = FolderType.encoder;

const EncoderStack = createStackNavigator<EncoderStackParams>();

const FolderComponent = memo(() => (
  <FolderPage folderType={folderType} />
));
const FolderLibraryComponent = memo(() => (
  <FolderLibrary dataHook={useFolders(folderType, false)} />
));
export const createEncoder = () => {
  const EncoderProjectScreen = memo(() => <EncoderProject />);
  const EncoderStackScreen = () => {
    return (
      <EncoderStack.Navigator
        screenOptions={{
          headerShown: false,
          freezeOnBlur: true,
        }}>
        <EncoderStack.Screen
          name="EncoderProjects"
          component={FolderLibraryComponent}
        />
        <EncoderStack.Screen
          name="EncoderProject"
          component={EncoderProjectScreen}
        />
        <EncoderStack.Screen
          name="Folder"
          component={FolderComponent}
        />
      </EncoderStack.Navigator>
    );
  };
  return EncoderStackScreen;
};
