export async function hashData(algo = 'SHA-256', payload: string) {
  const encoder = new TextEncoder();
  const encoded = encoder.encode(payload);
  const hash = await window.crypto.subtle.digest(algo, encoded);
  return getHexFromArrayBuffer(hash);
}

export async function verifySignature(
  key: JsonWebKey,
  signature: string,
  payload: string,
) {
  console.info('verify with key', key);
  const k = await window.crypto.subtle.importKey(
    'jwk',
    key,
    algoPSS,
    true,
    ['verify'],
  );
  const encoder = new TextEncoder();
  const encoded = encoder.encode(payload);
  const encodedSign = hexToArrayBuffer(signature);
  const verified = await window.crypto.subtle.verify(
    PSSParams,
    k,
    encodedSign,
    encoded,
  );
  return verified;
}

export async function signData(key: JsonWebKey, payload: string) {
  const k = await window.crypto.subtle.importKey(
    'jwk',
    key,
    algoPSS,
    true,
    ['sign'],
  );
  const encoder = new TextEncoder();
  const encoded = encoder.encode(payload);
  const sign = await window.crypto.subtle.sign(PSSParams, k, encoded);
  return getHexFromArrayBuffer(sign);
}

export function getKeyMaterial(password: string) {
  const enc = new TextEncoder();
  return window.crypto.subtle.importKey(
    'raw',
    enc.encode(password),
    'PBKDF2',
    false,
    ['deriveBits', 'deriveKey'],
  );
}

export const createChunks = (file: File) => {
  const cSize = maxSize;
  let startPointer = 0;
  let endPointer = file.size;
  let chunks = [];
  while (startPointer < endPointer) {
    let newStartPointer = startPointer + cSize;
    chunks.push(file.slice(startPointer, newStartPointer));
    startPointer = newStartPointer;
  }
  console.info(
    `total chunks: ${chunks.length} for file ${file.name}`,
  );
  return chunks;
};

export const decryptPayloadD = async (encData: Object) => {
  const shopKeys = await getShopKey();
  const shopK = shopKeys.keyPair.privateKey;
  try {
    console.info('encData', encData);
    const key = await decryptAesKey(
      encData.encKey,
      shopK as JsonWebKey,
    );
    const {encryptedPayload, iv} = encData.payload;
    const userData = JSON.parse(
      await decryptPayload(
        key,
        encryptedPayload as string,
        iv as string,
      ),
    );
    return userData;
  } catch (err) {
    console.error(err);
  }
};

export async function encryptWithPassword(
  password: string,
  plaintext: string,
) {
  const keyMaterial = await getKeyMaterial(password);
  const iv = window.crypto.getRandomValues(new Uint8Array(12));
  const salt = window.crypto.getRandomValues(new Uint8Array(12));
  const enc = new TextEncoder();
  const dec = new TextDecoder();
  const key = await window.crypto.subtle.deriveKey(
    {
      name: 'PBKDF2',
      salt,
      iterations: 100000,
      hash: 'SHA-256',
    },
    keyMaterial,
    {name: 'AES-GCM', length: 256},
    true,
    ['encrypt', 'decrypt'],
  );

  const encrypted = await window.crypto.subtle.encrypt(
    {name: 'AES-GCM', iv},
    key,
    enc.encode(plaintext),
  );
  return {
    encrypted: getHexFromArrayBuffer(encrypted),
    iv: getHexFromArrayBuffer(iv),
    salt: getHexFromArrayBuffer(salt),
  };
}
