export const recursiveShare = async (
  media: FolderPreviewContentType,
): Promise<void> => {
  if ((media as Media).isDownloaded) {
    try {
      shareFileToWT(media.id, media.folderId);
      return;
    } catch (err) {
      // console.error("shareFileToWT", err)
    }
  }
  if (
    (media as Folder).isFolder ||
    (media as EditorProject).isEditorProject ||
    (media as EditorLayer).isEditorLayer
  ) {
    const innerContent: (Media | Folder)[] = await getFolderContent(
      media.id,
    );
    await Promise.all(innerContent.map(recursiveShare));
    return;
  }
  return undefined;
};

