export const getMediaAndEnq = async (
  mediaId: string,
  filename: string,
) => {
  const media: Media = (await getById(mediaId)) as any;
  console.info('playing media', media);
  if (media.extractor === 'local') {
    media.url = (await getFileOpfs(media)) as string;
  }
  return enqMedia(media, filename);
};

export const enqMedia = async (media: Media, filename: string) => {
  return playMediaLink(media.url, media.currentPosition, filename);
};
