export const initUserData = async () => {
  try {
    UserData.user = await doInitialMigration(writeItem, getUserData);
    const userAgent =
      ExpoDevice.designName?.replace(/[^\w\s]/gi, '') ||
      'unknown_agent';
    const os = ExpoDevice.osName || 'unknown_os';
    const browserName = ExpoDevice.brand || 'web';
    console.info('browserName', browserName);
    const deviceId = userAgent + os + browserName;
    // console.info('deviceId', deviceId)
    const device = (await getById(deviceId)) as Device;
    // console.info('device', device)
    if (!device || !device.manufacturer) {
      const ndevice: Device = {
        id: deviceId,
        manufacturer: browserName,
        isDevice: true,
        userAgent,
        createdAt: new Date().valueOf(),
        model: browserName,
        os,
        platform: Platform.OS,
      };
      console.info(ndevice);
      await writeItem(ndevice);
    }
    console.info('initUserData', UserData);
    if (Platform.OS === 'web') {
      // startUpMediaSharing();
    }
  } catch (err) {
    console.error('initUserData Error', err);
  }
};

