export const extractMedia = async (link: string) => {
  const media = await getWT(link);
  console.info('onMedia', media);
  return {
    url: media.magnetURI,
    title: media.name,
    originalId: media.infoHash,
    id: media.infoHash,
    filename: media.files[0].name,
    extractor: 'wt',
    isFolder:
      media.files && media.files?.length > -1 ? true : undefined,
    files:
      media.files && media.files?.length > -1
        ? media.files
        : undefined,
  };
};
