import React, {memo} from 'react';
import FolderPage from '../components/folder/folderpage';
import {FolderType} from '../types';
import {FileStackParams} from '../types/nav';
import {createNativeStackNavigator} from '@react-navigation/native-stack';
import {AuctionPage} from '../components/auction/auctionpage';
import FolderLibrary from '../components/folder/folderlibrary';
import {useFolders} from '../hooks/useFolders';

const folderType = FolderType.media;

const FileStack = createNativeStackNavigator<FileStackParams>();

const FolderComponent = memo(() => (
  <FolderPage folderType={folderType} />
));
const FolderLibraryComponent = memo(() => (
  <FolderLibrary dataHook={useFolders(folderType, false)} />
));
export const createFileStack = () => {
  const FileStackScreen = () => {
    return (
      <FileStack.Navigator
        screenOptions={{
          headerShown: false,
          freezeOnBlur: true,
        }}>
        <FileStack.Screen
          name="Files"
          options={{orientation: 'portrait'}}
          component={FolderLibraryComponent}
        />
        <FileStack.Screen
          name="Auction"
          options={{orientation: 'portrait'}}
          component={AuctionPage}
        />
        <FileStack.Screen
          name="Folder"
          options={{orientation: 'portrait'}}
          component={FolderComponent}
        />
      </FileStack.Navigator>
    );
  };
  return FileStackScreen;
};
