import React, {
  useState,
  useEffect,
  useRef,
  useContext,
  useCallback,
} from 'react';
import {ChatMessage, Contact} from '../../types/Contact';
import {UserInfo} from '../../types/UserInfo';
import {View, TouchableOpacity, Platform} from 'react-native';

import {UserCtx} from '../../ctx';

import {useMessages} from '../../hooks/useMessages';
import {
  ActivityIndicator,
  List,
  ProgressBar,
  useTheme,
} from 'react-native-paper';

import {
  Bubble,
  BubbleProps,
  Composer,
  GiftedAvatar,
  GiftedChat,
  InputToolbar,
} from 'react-native-gifted-chat';
import {textAccentColor} from './styles';
import {Auction} from '../../types';

export interface ChatViewParams {
  chatId: string;
  params: {contact: Contact | Auction; user: UserInfo};
  isEmbeded?: boolean;
}
type OnSendType = {
  onSend: (t: {text: string}[], b: boolean) => void;
  focus: () => void;
};

const ChatView = (chatViewParams: ChatViewParams) => {
  const {chatId, params} = chatViewParams;

  const {user} = params;
  const {messages, sendMessage, nextPage, isLoadingEarlier} =
    useMessages(chatId);

  const onSend = (text: string) => sendMessage(text);

  const current = useContext(UserCtx);

  const renderBubble = (
    props: Readonly<BubbleProps<ChatMessage>>,
  ) => {
    return (
      <Bubble
        {...props}
        textStyle={{
          left: {color: textAccentColor},
        }}
        wrapperStyle={{
          right: {
            backgroundColor: '#871e69',
            alignSelf: 'auto',
            maxWidth: '60%',
          },
          left: {
            backgroundColor: '#344868',
            alignSelf: 'auto',
            maxWidth: '60%',
          },
        }}
      />
    );
  };
  const theme = useTheme();
  const composerRef = useRef<typeof Composer | undefined>();
  const {surface} = theme.colors;
  return (
    <View
      style={{
        backgroundColor: current!.colors.backgroundColor,
        height: '100%',
        width: '100%',
        flexDirection: 'row',
      }}>
      <GiftedChat
        scrollToBottom={true}
        renderLoading={() => <ActivityIndicator size={72} />}
        messages={messages.map((m: ChatMessage) => {
          // console.info("messages", m)
          return {
            ...m,
            _id: m.id,
            user: {
              _id: m.userId,
              name: m.userName,
            },
          };
        })}
        onSend={([message]) => onSend(message.text)}
        user={{
          _id: user?.id,
          avatar: user?.photo,
        }}
        renderBubble={renderBubble}
        timeTextStyle={{}}
        renderAvatar={props => (
          <GiftedAvatar
            {...props}
            user={props?.currentMessage?.user}
            avatarStyle={{
              backgroundColor: 'transparent',
              borderWidth: 1,
              borderColor: surface,
            }}
          />
        )}
        listViewProps={{
          scrollEventThrottle: 400,
          onEndReached: () => {
            console.info('on end of chat reached', isLoadingEarlier);
            if (isLoadingEarlier === true) return;
            nextPage();
          },
        }}
        isLoadingEarlier={isLoadingEarlier}
        loadEarlier={true}
        onLoadEarlier={() => nextPage()}
        renderLoadEarlier={props => (
          <View style={{height: 100, width: '100%'}}>
            {props.isLoadingEarlier ? (
              <ActivityIndicator size={32} />
            ) : (
              <View style={{height: 100}} />
            )}
          </View>
        )}
        renderChatEmpty={() => (
          <View style={{transform: [{scaleY: -1}]}}>
            <List.Item
              left={p => (
                <List.Icon
                  {...p}
                  color={textAccentColor}
                  icon={'lock'}
                />
              )}
              titleStyle={{color: textAccentColor}}
              title={'Decrypting Messages...'}
            />
            <ProgressBar indeterminate visible={true} />
          </View>
        )}
        renderInputToolbar={inputProps => (
          <InputToolbar
            {...inputProps}
            containerStyle={{
              backgroundColor: 'black',
              borderColor: 'gray',
            }}
            primaryStyle={{borderColor: 'white'}}
            renderComposer={props => (
              <Composer
                ref={composerRef}
                {...props}
                textInputStyle={{
                  color: textAccentColor,
                  padding: 10,
                }}
                multiline={true}
                textInputAutoFocus={true}
                textInputProps={{
                  ...props.textInputProps,
                  // for enabling the Return key to send a message only on web
                  blurOnSubmit: Platform.OS === 'web',
                  onSubmitEditing: e => {
                    if (props.text && (props as OnSendType).onSend) {
                      (props as OnSendType).onSend(
                        [{text: props.text?.trim()}],
                        true,
                      );
                    }
                    Platform.OS === 'web' &&
                      setTimeout(() => {
                        (
                          e.nativeEvent as unknown as {
                            target: {focus: () => void};
                          }
                        ).target.focus();
                      }, 300);
                  },
                }}
              />
            )}
            renderSend={props => (
              <TouchableOpacity
                onPress={() => {
                  (props as OnSendType).onSend(
                    [{text: props.text as string}],
                    true,
                  );
                }}>
                <List.Icon
                  style={{padding: 10}}
                  {...props}
                  icon={'send'}
                />
              </TouchableOpacity>
            )}
          />
        )}
        placeholder="Write your message"
        showUserAvatar={false}
      />
    </View>
  );
};

export default ChatView;
